.header {
  @extend %clearfix;
  @include fixed-positioned;
  top: 0;
  background: rgba(27, 41, 53, 0.9);
  padding: rem(20px) 0;
  z-index: 400;

  // Move the header under toolbar.
  .toolbar-fixed &,
  .toolbar-vertical & {
    top: rem($toolbarTabHeight);
  }

  .toolbar-tray-open.toolbar-horizontal & {
    top: rem($toolbarTabHeight + $toolbarMenuHeight);// toolbar-tab plus toolbar-menu height.
  }

  &__logo {
    font-size: 0;
    float: left;
    padding-top: rem(20px);

    .main-logo {
      display: none;
      width: rem(130px);
    }

    .second-logo {
      width: rem(70px);
    }
  }

  &__btn {
    float: right;

    & > div {
      display: inline-block;
      font-size: 0;
      padding-right: rem($grid-gutter-width/2);
      padding-left: rem($grid-gutter-width/2);
      vertical-align: middle;
    }
  }
  
  .btn--menu {
    cursor: pointer;

    .js-nav & {
      .icon-menu:before {
        color: $blue-hover;
      }
    }
  }

  span[class*="icon-"] {
    &:before {
      color: $white;
      font-size: rem($headerIconSize);
    }
  }

  &__menu {
    .sub-menu {
      background-color: $deeper-charcoal;
      display: none;

      .container {
        width: auto;
      }
      
      @include breakpoint($tablet) {
        .app-ind-col ul > li > ul {
          column-count: 2;
          column-gap: rem(20px);
        }
      }
    }
  }

  .btn--search {
    cursor: pointer;
    font-size: 0;
    padding-top: rem(($headerHeight - $headerIconSize)/2);
    padding-bottom: rem(($headerHeight - $headerIconSize)/2);
  }

  .menu-bottom {
    @extend %clearfix;
    clear: both;
    padding:  rem(10px) rem(15px) 0 rem(15px);

    .block {
      float: left;
    }
  }

  @include breakpoint($phone-only) {
    &__logo {
      .second-logo {
        width: rem(50px);
      }
    }

    &__btn {
      .btn--menu {
        padding: 0;
      }
    }
  }


  &__logo {
    @include breakpoint($mobile-landscape) {
      padding-top: rem(15px);
    }

    @include breakpoint($desktop) {
      padding-top: rem(19px);
    }
  }

  @include breakpoint($mobile-only) {
    border-bottom: rem(1px) solid $blue-hover;
    padding: 0;

    &.sticky {
      @include transition(all ease-in .2s);
      top: 0;
      z-index: 600;
    }

    &__menu {
      background-color: $deeper-charcoal;
      display: none;

      .parent-item {
        border-top: rem(1px) solid $grey-border;
      }

      .sub-menu {
        max-height: 50vh;
        overflow-y: auto;
      }
    }

    &__top-mobile {
      top: 0;
      overflow: hidden;
      z-index: 100;

      .search-block-form {
        background-color: $deeper-charcoal;
        display: none;
        position: absolute;
        width: 100%;
        top: 100%;
        padding: rem(20px) rem(15px);
      }
    }

    .header-right {
      float: none;

      &__item {
        display: block;

        .parent-item {
          @include grotesk-regular;
          @include mobile-menu-style;
          background: none;
          border-radius: 0;
          border-top: rem(1px) solid $grey-border;
        }

        .expanded {
          .parent-item {
            &:before {
              content: "\7d";
              color: $vibrant-blue;
            }
          }
        }
      }

      .block--phone,
      .btn--search,
      .icon-earth {
        display: none;
      }
    }

    .nav--account {
      a {
        @include mobile-menu-style;
        border-top: rem(1px) solid $grey-border;

        &:before {
          content: '\65';
        }
      }
    }

    .js-nav & {
      // Display and scroll only the menu, not the entire page.
      bottom: 0;
      overflow-y: auto;
    }

    .btn--search {
      .js-search & {
        background-color: $deeper-charcoal;

        .icon-search:before {
          color: $blue-hover;
        }
      }
    }

    .menu-col {
      .btn,
      .block {
        margin-bottom: rem(20px);
      }
    }

    .header-search-form {
      // On mobile search block will be from header-btns region.
      display: none !important;
    }

    .menu-bottom {
      .btn {
        margin-bottom: rem(15px);
      }
    }
  }

  @include breakpoint($tablet) {
    padding: 0;

    &__menu {
      display: block !important;

      .expanded {
        .parent-item {
          background-color: $deeper-charcoal;
        }
      }
    }

    &__btn {
      display: none;
    }

    &__top-mobile {
      .search-block-form {
        display: none !important;
      }
    }

    .sub-menu {
      @include fixed-positioned;
      padding: rem(30px);
      // Let long dropdowns to be scrolled.
      max-height: 100vh;
      overflow-y: auto;
      z-index: 999;
      top: rem($headerHeight);

      .toolbar-fixed &,
      .toolbar-vertical & {
        top: rem($headerHeight + $toolbarTabHeight);
      }

      .toolbar-tray-open.toolbar-horizontal & {
        top: rem(80px + $headerHeight); // toolbar-tab plus toolbar-menu plus header height.
      }

      .row {
        @include display(flex);
        @include flex-wrap(wrap);
      }

      .menu-bottom {
        padding-left: rem(25px);
        width: 100%;
      }
    }

    &-left {
      z-index: 2;

      .menu-col {
        border-left: rem(1px) solid $grey-border;

        &:first-child {
          border: none;
        }

        .btn {
          position: absolute;
          bottom: 0;
          left: rem(25px);
        }

        .block {
          margin-left: rem(10px);
          margin-right: rem(10px);
        }

        &.bottom-btn {
          padding-bottom: rem(80px); // Reserve space for bottom button.
        }
      }
    }

    .header-left {
      padding: 0;
    }

    .block-buyquote {
      & > nav {
        & > ul {
          & > li {
            padding: rem(16px) rem(5px) rem(17px);

            &.expanded {
              background-color: $deeper-charcoal;

              .parent-item {
                background-color: $blue-hover;
              }
            }
          }
        }
      }

      .parent-item {
        @include helvetica-arial-book;
        letter-spacing: rem(0.6px);
      }
    }

    .btn--search {
      padding-left: rem(10px);
      padding-right: rem(10px);
    }

    .buyquote-phone {
      display: none; // Hide phone to fit main-menu.
    }

    .region--header_menu_right {
      padding-left: 0;
      padding-right: 0;
    }
  }

  @include breakpoint($desktop) {
    &__logo {
      .main-logo {
        display: block;
      }

      .second-logo {
        display: none;
      }
    }

  }

  @media all and (min-width: 1110px) {

    .buyquote-phone {
      display: block;
      font-size: rem(15px);
      padding-right: rem(10px);
    }
   
  }

  @media all and (min-width: 992px) and (max-width: 1026px) {

    .header-left {
      width: 43%; // Rewrite grid column width to fit header's items.
    }

    .region--header_menu_right {
      width: 40%; // Rewrite grid column width to fit header's items.
    }

  }

  @media all and (min-width: 768px) and (max-width: 840px) {
    .nav--main > ul > li > a {
      padding: 1.5625rem 0.4rem;
    }
    span[class*="icon-"]::before {
      font-size: 1.2rem;
    }
    .block-buyquote .parent-item {
      font-size: 0.6rem;
    }
    &__logo .second-logo {
    width: 3.375rem;
    }
  }

  @include breakpoint($desktop-wide) {
    padding-left: rem($grid-gutter-width/2);
    padding-right: rem($grid-gutter-width/2);

    .header-left {
      width: 36%;
    }

    .region--header_menu_right {
      width: 46%;
    }

    .header__logo {
      padding-right: 0;
      width: 13%;
      padding-left: rem(10px);
      .main-logo {
        width: rem(140px);
      }
    }

    &__menu {
      .sub-menu {
        .container {
          width: $container-xl;
          max-width: 100%;
        }
      }

      .region--header_menu_right {
        width: 50%; // Rewrite grid column width to fit header's items.
      }
    }
  }
}

// When the mobile menu is opened, hide the scroll for the body.
@include breakpoint($mobile-only) {
  .js-nav {
    body {
      overflow: hidden;
    }
  }
}

// Header menu.

.nav--main {
  @include grotesk-regular;

  & > ul {
    & > li {
      & > a {
        @include mobile-menu-style;
      }

      &.expanded {
        &  > a {
          &:before {
            @extend .icon-chevron-up;
            color: $vibrant-blue;
          }
        }
      }
    }
  }
  
  .menu-col {
    ul {
      font-size: rem(14px);
      padding: 0 rem(10px);

      a {
        display: block;
      }
    }

    & > ul,
    div > ul {
      & > li {
        padding-bottom: rem(10px);

        & > a {
          color: $vibrant-blue;
          font-size: rem(15px);
          padding-bottom: rem(5px);
          text-transform: uppercase;
        }
      }

      ul {
        a {
          color: $white;
          letter-spacing: rem(.3px);
          padding: rem(10px) 0;
        }
      }
    }
  }

  .menu-bottom {
    .btn {
      float: right;
    }
  }

  @include breakpoint($tablet) {
    & > ul {
      & > li {
        float: left;

        & > a {
          font-size: rem(12px);
          line-height: rem(15px);
          padding: rem(25px) rem(11px);
          
          &:hover {
            background-color: $deeper-charcoal;
          }

          &:before {
            content: none !important;
          }
        }
      }
    }

    .menu-col {
      ul {
        ul a {
          padding: 0 0 rem(10px);
        }
      }
    }
  }

  @include breakpoint($desktop-wide) {
    & > ul {
      & > li {
        & > a {
          font-size: rem(13px);
          padding: rem(25px) rem(11px);
        }
      }
    }
  }
}

.header-right {
  @include helvetica-arial-light;
  float: right;

  &__item {
    display: inline-block;

    &.icon-earth {
      color: $white;
      padding-left: rem(10px);
      padding-right: 5px;
      
      @include breakpoint($mobile-only) {
        float: right;
        padding-top: rem(20px);
      }

      &:before {
        font-size: rem(24px);
      }
      .text {
        font-size: rem(14px);
        padding-left: rem(5px);
        float: right;
        margin-top: rem(3px);
      }
    }
  }

  .header-search-form {
    display: none;
  }

  @include breakpoint($tablet) {
    .nav--account {
      li {
        display: inline-block;
        font-size: 0;
        vertical-align: middle;

        &:first-child {
          padding-right: rem(10px);

          a:before {
            content: '\25';
            color: $white;
            font-size: rem(24px);
          }
        }

        &:last-child {
          font-size: rem(12px);
          line-height: rem(24px);
          padding: 0;

          a:before {
            content: none;
          }
        }

        a,
        a:hover {
          color: $white;
        }
      }
    }

    &__item,
    .header-search-form {
      vertical-align: middle;
    }

    &__item {
      .parent-item {
        &:before {
          content: none !important;
        }
      }
    }

    & > div {
      display: inline-block; // Fix style for search block wrapper for logged in.
    }
  }

  @include breakpoint($desktop-wide) {
    .btn--search {
      display: none;
    }

    .header-search-form {
      display: inline-block !important;
      width: rem(200px);
    }
  }
}
