$logoWidth: 50px;
$padLeftDesktop: $logoWidth + $grid-gutter-width;
$padLeftDesktopWide: $logoWidth + $grid-gutter-width*2;

.footer {
  @extend %clearfix;
  @include grotesk-regular;
  position: relative;
  background-color: $deep-charcoal;
  font-size: rem(14px);
  letter-spacing: rem(0.6px);
  padding-top: rem(15px);
  padding-bottom: rem(15px);
  margin-top: rem(-1px); // Fix subpixel rounding error for images above the footer.

  &__logo {
    margin: 0 auto rem(20px);
    width: rem($logoWidth);

    img,
    svg {
      height: 100%;
    }
  }

  &__content--chinaicp {
    padding-top: rem(10px);
    p {
      font-size: rem(13px); 
      color: $gray-c; 
      padding:0; 
      margin:0;
    }
    a {
      color: $white
    }
  }

  &,
  a {
    color: $gray-c;
  }

  nav {
    @extend %clearfix;

    a {
      float: left;
      margin-bottom: rem(20px);
      width: 50%;
    }
  }

  .main-nav {
    &,
    a {
      color: $white;
      text-transform: uppercase;
    }
  }

  .social-links {
    margin-bottom: rem(30px);

    a {
      display: inline-block;
      font-size: 0;
      margin-left: rem(25px);
      text-decoration: none;

      &:first-child {
        margin: 0;
      }

      &:before {
        font-size: rem(30px);
        letter-spacing: 0;
      }
    }

    .icon-weibo,
    .icon-wechat {
      background-color: $gray-c;
      border-radius: rem(2px);
      color: $deep-charcoal;
    }
  }

  .copyright {
    color: $gray-c;
  }

  .second-nav {
    li {
      &:last-child {
        a {
          &:after {
            content: none;
          }
        }
      }
    }
  }

  @include breakpoint($tablet) {
    &.footer {
      padding-left: rem($padLeftDesktop);
    }

    &__logo {
      position: absolute;
      left: rem($grid-gutter-width/2);
      top: rem($grid-gutter-width/2);
    }

    .social-links {
      text-align: right;
    }
    
    .social-links {
      margin-bottom: rem(10px);

      a {
        margin-left: rem(14px);

        &:before {
          font-size: rem(24px);
        }
      }
    }

    .main-nav {
      a {
        margin-left: rem(30px);
        width: auto;
      }

      li:first-child {
        a {
          margin: 0;
        }
      }
    }

    .second-nav {
      a {
        position: relative;
        margin: 0 rem(30px) rem(5px) 0;
        width: auto;

         &:after {
           content: '\007C';
           position: absolute;
           width: rem(30px);
           text-align: center;
         }
      }
    }

    .copyright {
      padding-top: rem(15px);
    }
  }

  @include breakpoint($desktop-wide) {
    &.footer {
      padding-left: rem($padLeftDesktopWide);
      padding-right: rem($grid-gutter-width);
    }

    &__logo {
      left: rem($grid-gutter-width);
    }

    .copyright {
      padding-top: 0;
      text-align: right;
    }
  }
}

#block-tek-addthis,
#block-addthis-china {
  position: relative;

  .addthis_toolbox {
    bottom: rem(45px);
  }

  @include breakpoint($tablet) {
    .addthis_toolbox {
      bottom: rem(25px);
    }
  }
}

.addthis_toolbox {
  display: none;
  position: absolute;
  background-color: $white;
  position: absolute;
  z-index: 5;
  padding: rem(5px);
  margin: 0;
  border: $medium-gray solid rem(1px);
  display: none;
  width: rem(150px);

  &_item {
    max-width: rem(50px);
  }

  &__close {
    @extend %icons;
    @extend .icon-close;
    color: $light-grey;
    cursor: pointer;
    font-size: rem(12px);
    font-weight: bold;
    position: absolute;
    right: rem(5px);
    top: rem(10px);
  }

  &.addthis-show {
    display: block;
  }
}
