main {
  padding-top: rem($headerHeight);

  & > .container {
    @extend %margin-bottom;
  }
}

.layout-container {
  margin: 0;
}

.layout-content {
  @extend %row-sm;
  @include make-md-column(8);
}

.layout-sidebar--right,
.layout-sidebar--left {
  @extend %row-sm;
  @include make-md-column(4);
}

// Product Series page.

.field--field_subtitle {
  @include grotesk-light;
  font-size: rem(22px);
  margin-bottom: rem(20px);
}

.support-info-label {
  padding: rem(10px) 0;

  a {
    color: $base-font-color;
    text-decoration: underline;
  }
}

.price-band {

  margin-bottom: rem(32px);

  .left,
  .right {
    @include display(flex);
    @include flex-direction(column);
    padding-top: rem(40px);
    padding-bottom: rem(40px);
  }

  .left {
    @include justify-content(space-between);
    background-color: $grey-blue;
  }

  .right {
    @include justify-content(center);
    background-color: $vibrant-blue;

    .btn {
      @include align-self(center);
      border-color: $white;
    }
  }

  &__price,
  &__dropdown {
    @include align-self(center);
  }

  &__price {
    @include grotesk-light;
    color: $white;
    font-size: rem(24px);
    text-transform: uppercase;

    .price-label {
      @include helvetica-arial-light;
      color: $lightest-grey;
      font-size: rem(12px);
      margin-bottom: rem(3px);
    }

    a {
      color: $white;
      text-transform: none;
    }
  }

  a.btn,
  &__dropdown {
    margin: rem(10px);
  }

  .btn {
    min-width: rem(130px);
  }

  &__dropdown {
    @extend %dropdown-btn;

    .btn {
      min-width: rem(165px); // Make buttons with dropdown wider.
    }
  }

  @include breakpoint($mobile-landscape) {
    .left,
    .right {
      @include flex-direction(row);
    }

    &__price {
      margin: 0;
    }
  }

  @include breakpoint($tablet) {
    @include display(flex);
    width: 100%;
  }
}

.node-landing {
  .multi-line {
    @extend %padding-top;
  }
}

// Related Products Listing

.related-products {
  margin-top: rem(35px);
}
// Datasheet page.

.dropdown {
  @extend %dropdown-btn;
  display: inline-block;
  margin-bottom: rem(20px);

  &.opened {
    z-index: 22;
    
    .btn {
      background-color: transparent;
      border-color: $tk-blue;
      color: $grey-3;

      &:before {
        color: $tk-blue;
      }
    }
  }
}

.datasheet {
  @include helvetica-arial-light;
  color: $medium-gray;

  h5 {
    @extend %section-title;
  }
  
  .color-red {
    @extend %padding-top;
  }

  .price {
    font-size: rem(26px);
    margin-bottom: rem(15px);

    h5 {
      margin-bottom: 0;
    }

    span {
      color: $vibrant-blue;
    }
  }

  @include breakpoint($mobile-tablet-only) {
    .left {
      margin-bottom: rem(20px);
    }
  }

  @include breakpoint($tablet) {
    @include display(flex);
    @include align-items(center);
  }
}

.node-datasheet {
  .tek-tabs__content {
    .double-scroll {
      overflow-x: auto;
      overflow-y: hidden;
    }

    table {
      th, td {
        word-break: normal;
        word-wrap: normal;
      }
      caption {
        text-align: left;
        font-size: rem(17px);
        padding-bottom: rem(10px);
      }
    }

    h2.sectiontitle {
      clear: both;
      font-size: rem(22px);
      padding-top: rem(20px);
    }

    h4.topictitle1 {
      @include grotesk-light;
      font-size: rem(26px);
      padding-top: 0;
    }

    h4 {
      font-size: rem(20px);
      font-weight: normal;

      &.pagebreak {
        clear: none;
      }
    }

    dl + p  {
      clear: both;
    }

    .figcap,
    .small {
      font-size: rem(12px);
      padding: rem(10px) 0;
    }

    h5 {
      font-size: rem(16px);
      clear:both;

      &.topictitle1 {
        font-size: rem(18px);
        margin: 20px 0 8px 0;
      }
    }

    dl {
      clear: left;
      margin-top: rem(20px);

      dl {
        margin-top: 0;
      }
    }

    dt {
      clear: left;
      float: left;
      padding-right: rem(40px);
      margin: 0 0 rem(10px);
      width: 25%;
      font-size: rem(15px);

      @include breakpoint($mobile-tablet-only) {
        width: 100%;
      }
    }

    dd {
      float: left;
      margin: 0 0 rem(10px);
      width: 75%;
      color: $light-grey;
      font-size: rem(15px);

      @include breakpoint($mobile-tablet-only) {
        width: 100%;
      }

      dd {
        padding-left: rem(20px);
      }

      dt,
      dd {
        width: 100%;
      }
    }
  }

  header {
    h3 {
      @extend %section-title;
    }

    img {
      margin: 0 auto;
    }
  }
}

// Landing page.
.node-landing {
  .view-mode-overview,
  .media-video {
    margin-bottom: rem(20px);
  }

  .media-video {
    @extend %responsive-video;
  }

  @include breakpoint($mobile-landscape) {
    .view-mode-overview,
    .media-video {
      float: right;
      margin-left: rem(20px);
    }

    .media-video {
      width: 50%;
    }
  }

  @include breakpoint($mobile-only) {
    .left-col {
      margin-bottom: rem(20px);
    }
  }
}

// Blocks.
.block--grey {
  @extend %row-sm;
  background-color: $tab-select;
  border: rem(1px) solid $grey-e5;
  padding: rem($grid-gutter-width / 2);

  @include breakpoint($tablet) {
    padding: rem($grid-gutter-width);
  }
}

.block--top-downloads {
  @include helvetica-arial-light;
  font-size: rem(14px);

  .btn {
    margin-top: rem(10px);
  }

  .views-row {
    margin-bottom: rem(20px);
  }
}

.node--lab_course {
  .field--comment__title {
    @include helvetica-arial-book;
    font-size: rem(18px);
    margin: rem(20px) 0 rem(10px);
  }
}

// Careers page. Migrated markup.
#career-page {
  .carousel {
    article {
      &:first-of-type {
        display: none;
      }

      @include breakpoint($tablet) {
        display: none;

        &:first-of-type {
          display: block;
        }
      }
    }
  }

  // Rewrite bootsrap columns, as in D8 breakpoints differ from D6 ones.
  .col-sm-6,
  .col-sm-4 {
    width: 100%;
  }

  @include breakpoint($tablet) {
    .about-tek {
      margin: 0;
    }

    .col-sm-6 {
      width: 50%;
    }

    .career-box {
      width: calc(100% / 3 - 18px); // Calculate for career-box.
    }
  }
}

.about-tek,
.tek-innovating,
.tek-community {
  padding: rem(20px);

  @include breakpoint($desktop) {
    padding: rem(50px);
  }
}

.tek-innovating {
  border-bottom: 1px solid $gray-c;
}

.tek-community {
  ul {
    @extend %small-font;

    li {
      color: $base-font-color;
    }
  }
}

.carousel,
.career-search {
  @extend %row-sm;
}

.career-search {
  @extend %padding-v;
  @include background-image(linear-gradient( to right, $vibrant-blue 60%, $tk-blue ));
  padding-left: $grid-gutter-width / 2;
  padding-right: $grid-gutter-width / 2;

  h3 {
    color: $white;
  }

  .btn {
    background-color: $tk-blue;
  }

  @include breakpoint($tablet) {
    @include display(flex);
    @include justify-content(center);
    background-image: url($image-path + 'careers_searchbar.jpg');
    background-size: cover;

    h3 {
      margin: 0;
    }

    .btn {
      @include align-self(flex-start);
      margin-left: rem(20px);
    }
  }
}

.career-box {
  @include background-image(linear-gradient(to bottom right, $deep-charcoal , $grey-blue));
  padding: rem(35px);
  margin-bottom: rem(20px);

  .h12 {
    @include grotesk-light;
    color: $vibrant-blue;
    font-size: rem(22px);
  }

  p {
    color: $white;
  }

  @include breakpoint($tablet) {
    background-image: url($image-path + 'careers_col_boxes.jpg');
    background-size: cover;
    height: rem(267px);
    margin-bottom: rem(50px);

    &.with-margin {
      margin: 0 rem(27px);
    }
  }
}

// Product-support.
.product-support-footer-links {
  clear: both;
  padding: rem(30px) 0;
  .top-icon {
    display: block;
    margin-bottom: rem(20px);
    padding: 0  rem($grid-gutter-width / 2);
    text-align: center;

    &:before {
      color: $medium-gray;
    }

    &:hover {
      &:before {
        color: $link-hover;
      }
    }
  }
  
  @include breakpoint($mobile-landscape) {
    @include display(flex);
    @include justify-content(space-around);

    .top-icon {
      @include flex(1);
    }
  }

  @include breakpoint($tablet) {
    .top-icon:before {
      font-size: rem(60px);
    }
  }
}

.product-support-results {
  @extend %padding-top;
  border-top: rem(1px) solid $light-gray;
  margin-top: rem(40px);
}
